        "subject": "FW: Bishop\u0027s Corner",
        "body": "\n\n -----Original Message-----\nFrom: \tBill Montez \u003cmr-bill1@swbell.net \u003cmailto:mr-bill1@swbell.net\u003e\u003e@ENRON [mailto:IMCEANOTES-Bill+20Montez+20+3Cmr-bill1+40swbell+2Enet+3E+40ENRON@ENRON.com] \u003cmailto:[mailto:IMCEANOTES-Bill+20Montez+20+3Cmr-bill1+40swbell+2Enet+3E+40ENRON@ENRON.com]\u003e \nSent:\tFriday, July 20, 2001 4:46 AM\nTo:\tPhillip Allen\nSubject:\tBishop\u0027s Corner\n\n\nPhillip,\n \nAttached are the following completed forms we worked out together.   Please review and, if acceptable, execute in blue ink. \n \n1. 92530 Previous Participation Cert.\n2. 92417 Personal Financial Statement\n3. 92013 Supplement Banking \u0026 Credit References\n4. Verification of Deposits/loans(VOD) 3( Reflecting 2013supp info.\n5. 935.2 Affirmative Fair Housing Marketing Plan.\n \nI have received fax copies of the following executed documents:\n \n1.  Release of Banking and Credit Info.\n2. Byrd Amendment Cert\n3. 2010 Equal Opportunity Cert\n4. Tax Credit Statement\nPlease send me the original blue ink excuted copes of these four  documents.  This should complete all the mortgage credit and FHEO  documents.  Other documents to be executed will include the following which  will be forwarded to you when completed:\n1.    B181 Owner/Architect Agreement with HUD  Amendment\n2.   Architect Certificate\n3.   Owner/Lender Identity of Interest Cert.\n4.  Other identity of Interest Cert.\n5.   2328 Mortgagor\u0027s/Contractor Cost Breakdown\n6.   HUD-92013 Application for Multifamily Project \n7. HUD-9839B Management Agent Certification\n8. W-9 Request for Taxpayer ID #(owner, principals)\n \nI will endeavor to make this an easy process for you so you can work out  the bigger issues with Greg regarding design, construction, marketing and  management.\nPlease call me if you need anything or have any questions.\n \nBest Regards\nBillmr_bill@texas.net mr-bill1@swbell.net  (home  office)\n210.541.0444\n \n \n\n - Blank Bkgrd.gif \n - Hud2530.pdf \n - 92417.pdf \n - 2013Supplement.PDF \n - VOD(AUTO).PDF \n - VOD(Leander).PDF \n - VOD(Paine).PDF \n - 935.2(SA).PDF ",
