        "body": "Jeff Gosset is going to pull the P\u0026L packet from the days with the biggest discrepancy and we will go from there.\n\n -----Original Message-----\nFrom: \tHayden, Frank  \nSent:\tMonday, December 10, 2001 12:34 PM\nTo:\tAllen, Phillip K.\nCc:\tPort, David\nSubject:\t\n\nHow are we handling the Steven Allen?\n",
