        "subject": "FW: Wildflower, Rayburn, Emilie apts",
        "body": "\n\n -----Original Message-----\nFrom: \t\"JEFF SMITH\" \u003cjsmith@austintx.com \u003cmailto:jsmith@austintx.com\u003e\u003e@ENRON  \nSent:\tFriday, December 07, 2001 2:07 PM\nTo:\tAllen, Phillip K.\nSubject:\tFW: Wildflower, Rayburn, Emilie apts\n\nThree more deals in San Antonio.\n\n - emilie onsale.doc \n - rayburn onsale.doc \n - wildflower onsale.doc ",
