        "body": "\n\n -----Original Message-----\nFrom: \tHolst, Keith  \nSent:\tMonday, July 30, 2001 12:20 PM\nTo:\tAllen, Phillip K.\nSubject:\tFW: Enron\u0027 s August Baseload Physical Fixed Price Transactions as of 07/27/01\n\n\n\n -----Original Message-----\nFrom: \tBike, Anne  \nSent:\tFriday, July 27, 2001 10:16 PM\nTo:\tAllen, Phillip K.; Distribution Prices - L Kuch (E-mail); Ermis, Frank; GasDaily (E-mail); Gay, Randall L.; Grigsby, Mike; Holst, Keith; IFERC Liane Kucher (E-mail); kdoole - Publication Distribution (E-mail); Keavey, Peter F.; Kuykendall, Tori; Lenhart, Matthew; mhenergy - Publication Distribution (E-mail); Mike Grigsby @ Home (E-mail); NGW PUBLICATION (E-mail); Phillip Allen - Home (E-mail); Prices - Inteligence Press (E-mail); Reitmeyer, Jay; Sanchez, Monique; Scott, Susan M.; South, Steven P.; Tholt, Jane M.\nSubject:\tEnron\u0027 s August Baseload Physical Fixed Price Transactions as of 07/27/01\n\nI apologize for not sending yesterday\u0027s information.  We were experiencing technical difficulties.  Please let me know if you have any questions or comments.  Thank you.\n\nAnne Bike\n\n\n    ",
