        "body": "Thanks.  I am waiting to speak to Pauline Kuo to schedule a time and place to meet her this week.  Hopefully we can meet Tuesday or Wednesday.  I will swing by your office to pick up the documents.\n\nPhillip\n\n -----Original Message-----\nFrom: \tJacquesTC@aol.com@ENRON [mailto:IMCEANOTES-JacquesTC+40aol+2Ecom+40ENRON@ENRON.com] \nSent:\tMonday, September 10, 2001 9:54 AM\nTo:\tAllen, Phillip K.\nSubject:\tRe:\n\nPhillip, \n      I sent you a note that the changes have been made and faxed to Kevin.  \nCall me to arrange to pick up the originals or have them delivered where you  \nneed them.  \n                                                                         \nThanks, \n                                                                              \n Jacques",
