        "body": "According to Jacques, the release is with the Kuo\u0027s attorney, Kevin Kolb.  I just spoke to Kevin.  The Kuo\u0027s have been away on vacation.  He is going to try and get things finalized this week.  It sounds like they want to sign and exchange money in person.  This Saturday afternoon in Seguin will probably be  the most convenient time.  I will let you know when I hear more from Kevin Kolb.\n\nPhillip\n\n -----Original Message-----\nFrom: \t\"Jeff Smith\" \u003cjsmith@austintx.com\u003e@ENRON [mailto:IMCEANOTES-+22Jeff+20Smith+22+20+3Cjsmith+40austintx+2Ecom+3E+40ENRON@ENRON.com] \nSent:\tTuesday, September 04, 2001 7:35 AM\nTo:\tAllen, Phillip K.\nSubject:\tRE:\n\nAny word from Jaques on the Seguin deal?\n\n\u003e -----Original Message-----\n\u003e From: Allen, Phillip K. [mailto:Phillip.K.Allen@enron.com]\n\u003e Sent: Tuesday, September 04, 2001 9:14 AM\n\u003e To: jsmith@austintx.com\n\u003e Subject:\n\u003e\n\u003e\n\u003e Jeff,\n\u003e\n\u003e I got your voice mail about the school board approval.  That\u0027s good\n\u003e news.  Let me know when the closing is rescheduled.\n\u003e\n\u003e Phillip\n\u003e\n\u003e\n\u003e **********************************************************************\n\u003e This e-mail is the property of Enron Corp. and/or its relevant\n\u003e affiliate and may contain confidential and privileged material\n\u003e for the sole use of the intended recipient (s). Any review, use,\n\u003e distribution or disclosure by others is strictly prohibited. If\n\u003e you are not the intended recipient (or authorized to receive for\n\u003e the recipient), please contact the sender or reply to Enron Corp.\n\u003e at enron.messaging.administration@enron.com and delete all copies\n\u003e of the message. This e-mail (and any attachments hereto) are not\n\u003e intended to be an offer (or an acceptance) and do not create or\n\u003e evidence a binding and enforceable contract between Enron Corp.\n\u003e (or any of its affiliates) and the intended recipient or any\n\u003e other party, and may not be relied on by anyone as the basis of a\n\u003e contract by estoppel or otherwise. Thank you.\n\u003e **********************************************************************\n\u003e ",
