        "subject": "RE: Topic Framework Deliverable - Sign-Off for Acceptance",
        "body": "These are fine.\n\n -----Original Message-----\nFrom: \tMcDaniel, Kirk  \nSent:\tTuesday, October 23, 2001 1:36 PM\nTo:\tAllen, Phillip K.; O\u0027rourke, Tim; Frolov, Yevgeny\nSubject:\tTopic Framework Deliverable - Sign-Off for Acceptance\nImportance:\tHigh\n\nGentlemen\n\nGood day. I hope all is going well. \n\nIf I do not hear from you by COB tomorrow I will take your silence as acceptance and that their are no non-conformities to prevent acceptance of this deliverable. This action is necessary as contractually we are coming to the end of the acceptance period for this deliverable. \n\nGood news is that this deliverable and the High level design deliverable are the last two deliverables for the Design Phase of the project. Thanks\n\nCheers\nKirk\n---------------------- Forwarded by Kirk McDaniel/HOU/EES on 10/23/2001 03:31 PM ---------------------------\n\n\nKirk McDaniel\n10/19/2001 08:39 AM\nTo:\tPhillip K Allen/Enron@EnronXGate, Tim O\u0027Rourke/Enron@EnronXGate, Yevgeny Frolov/Enron@EnronXGate\ncc:\tEd McMichael/Enron@EnronXGate, Dutch Quigley/Enron@EnronXGate \nSubject:\tTopic Framework Deliverable - Sign-Off for Acceptance\n\nPhillip/Tim/Yevgeny \nThe attached zip file contains the 10 topic frameworks for sign off. Phillip you have reviewed these documents and given them the thumbs up prior to my input. The email below from Monica summarizies my minor input and after reading Monica email, if no red flags or concerns or raised you can again give your approval without necessarily reviewing the attachments again.\n\nTim and Yevegeny (If possible), please review the attachment, the email below and provide input no later then COB 10/22. Thanks\n\nCheers\nKirk\n\nNote: Please notice that Accenture is putting how long it takes them to complete tasks in their emails to us. \n\n\n - Topic Framework_Final.zip \u003c\u003c File: Topic Framework_Final.zip \u003e\u003e \n---------------------- Forwarded by Kirk McDaniel/HOU/EES on 10/19/2001 08:29 AM ---------------------------\n\n\nmonica.l.brown@accenture.com on 10/18/2001 11:04:02 AM\nTo:\tkmcdani@enron.com\ncc:\tsheri.a.righi@accenture.com \nSubject:\tTopic Framework Updates!\n\n\nHi Kirk,\n\nHere are the answers to some of your questions and concerns on the Topic\nFrameworks.  These updates, along with discussions with SMEs, took a total\nof one hour.\n\nAccounting and Reporting - (1) Deal Flow - \u003c\u003cAccording to Berney Aucoin\u003e\u003e\nthere is no difference between an Orginator/Marketer  on the deal flow,\nthey stated that a deal is a deal and it is the same for everyone.  (2)\nRoles associated within the Desk - \u003c\u003cAccording to Berney Aucoin \u003e\u003e Admin\nsupport and Commerical support should not be added.  We are concentrating\non the major roles within a desk, any more granular could be inaccurate\nbecause it is different for every company.  Admin support - answers the\nphones and Commerical support runs the books in the back office.\n\nCommodity Fundamentals - (1) Broker fees and Margin calls were added in the\noverview, \u003c\u003cAccording to Berney Aucoin\u003e\u003e it was worth mentioning, but\nwholesale is exempt from fees and taxes.  (2) Industrial, Retail and\nWholesale was added as an i.e., by consumption by consumer\n\nOrganizational Structure - (1) Communication - Inter/Intra department\ncommunications were added into the Challenges of Managing Trading Function\nand Trading System. Inter department communication (i.e., between\ndepartments within organization) and Intra department communications (i.e.,\nbetween roles on a desk after a trade/deal) (between desks, internal\ncommunication between Marketer, Originator, Structurer and Trader, after a\ntrade and reconciling position reports)\n\nRisk Management Inputs - Consequences within VaR guidelines was added to\n\"Trading outside of prescribed VaR guidelines\n\nSwaps - Types of credit classifications was added to Credit Review under\nProtecting Againist Loss.\n\nThe note for the references used was included on every topic framework -\n*Note: Please note in your assignments when using additional resources\nother than the BRM course material and your own knowledge. Please do not\ncopy and paste from Derivatives 1 or any other resource material. This will\nsignificantly help us deter any copyright infringements. Thank you\n\nLet me know if you have any questions!\n\nThanks,\nMonica L. Brown\nAccenture\nPDP Experienced Analyst\nHouston - 2929 Allen Parkway\nDirect Dial: +1 713 837 1749\nVPN \u0026 Octel: 83 / 71749\nFax: +1 713 257 7211\nemail: monica.l.brown@accenture.com\n\n\n\nThis message is for the designated recipient only and may contain\nprivileged or confidential information.  If you have received it in error,\nplease notify the sender immediately and delete the original.  Any other\nuse of the email by you is prohibited.\n\n---------------------- Forwarded by Kirk McDaniel/HOU/EES on 10/19/2001 08:29 AM ---------------------------\n\n\nsheri.a.righi@accenture.com on 10/18/2001 11:11:04 AM\nTo:\tkmcdani@enron.com\ncc:\tdonald.l.barnhart@accenture.com, monica.l.brown@accenture.com \nSubject:\tTopic Frameworks - Updates Completed\n\n\nKirk -\n\nThank you for your timely feedback. Per our discussions, we have updated\nall of the topic frameworks and attached them below. In addition, Monica\nhas sent you an email describing the changes she has made and explanations\naround why some changes weren\u0027t made (SME\u0027s responses to your questions).\n\nThese updates, along with discussions with SMEs, took a total of one hour.\n\n(See attached file: Topic Framework_Final.zip)\n\nSheri A. Righi\nAccenture\nHuman Performance Service Line\nHartford - One Financial Plaza\nDirect Dial: 860 756 2245\nVPN \u0026 Octel: 765 2245\ne-mail: sheri.a.righi@Accenture.com\n\n\nThis message is for the designated recipient only and may contain\nprivileged or confidential information.  If you have received it in error,\nplease notify the sender immediately and delete the original.  Any other\nuse of the email by you is prohibited.\n - Topic Framework_Final.zip \u003c\u003c File: Topic Framework_Final.zip \u003e\u003e \n\n\n",
