        "subject": "ANCHORDESK: Hope ahead: What I learned from 2001\u0027s tragedies",
        "body": "\n\n_____________________DAVID COURSEY_____________________\n\nHOPE AHEAD: WHAT I LEARNED FROM 2001\u0027S TRAGEDIES\n\n    As years go, 2001 sucked. But adversity teaches\n         us more important lessons than prosperity.\n         So my bet is that 2001 and the upcoming 2002\n         will prove to be very educational--and in\n         ways that matter. Here\u0027s what I learned this\n         year, as I saw technology--and the world--change\n         for good.\n\nhttp://cgi.zdnet.com/slink?/adeskb/adt1231/2835136:8593142\n\n\n#\n\n\n_____________________NEWS ANALYSIS_____________________\n\nSylvia Carr\n\n    P2P: NOW FOR BIZ... WHAT\u0027S HP\u0027S FATE?... OS X GAINS\nCONVERTS...\n\n    Peer-to-peer isn\u0027t just for Napster\n    anymore. The technology is taking hold\n    in businesses, universities, and the\n    armed forces. It\u0027s being used to find\n    a cure for cancer and to facilitate battlefield\n    communication. Is there anything P2P\n    can\u0027t do?\n\nhttp://cgi.zdnet.com/slink?/adeskb/adt1231/2835139:8593142\n\n\n\n\n\n_____________________EXPERT ADVICE_____________________\n\n\nWayne Rash\n\n  SPY GAMES: IS SOMEONE LEAKING YOUR COMPANY SECRETS?\n\n   Catching spies is probably pretty far down\n         your list of IT priorities. But if you knew\n         the damage even one unsavory employee could\n         do, you might think twice. Wayne Rash offers\n         a cautionary tale.\n\n\nhttp://cgi.zdnet.com/slink?/adeskb/adt1231/2835127:8593142\n\n\n\t\t\t\u003e \u003e \u003e \u003e \u003e\n\n\nLee Schlesinger\n\n  A MURKY CRYSTAL BALL? MY TECH PREDICTIONS FOR 2002\n\n   As 2001 draws to a close, Lee takes a peek into\n         a not-so-clear future. Here are his seven\n         predictions for what the tech sector will\n         bring in the coming year--from an instant-messaging\n         mess to a new wireless empire.\n\n\nhttp://cgi.zdnet.com/slink?/adeskb/adt1231/2835088:8593142\n\n\n\t\t\t\u003e \u003e \u003e \u003e \u003e\n\n\nPreston Gralla\n\n  RING IN THE NEW YEAR WITH THESE TIMELY DOWNLOADS\n\n   Want to make sure you\u0027re perfectly in sync\n         when 2002 rolls around? Then, says Preston,\n         spend a minute with these three utilities--they\u0027ll\n         get you organized in no time at all.\n\n\nhttp://cgi.zdnet.com/slink?/adeskb/adt1231/2835089:8593142\n\n\n\t\t\t\u003e \u003e \u003e \u003e \u003e\n\n\n_____________________CRUCIAL CLICKS_____________________\nMake sure you see these features on ZDNet today!\n\nIS THE WEB ON A DOWNWARD SPIRAL?\nhttp://www.zdnet.com/zdnn/stories/comment/0,5859,2834768,00.html\n\nHOW TO PUBLISH WITH POLISH--AT HOME\nhttp://www.zdnet.com/products/stories/reviews/0,4161,2831756,00.html\n\n\nMAKING SENSE OF WIRELESS STANDARDS\nhttp://www.zdnet.com/techupdate/stories/main/0,14179,2831727,00.html\n\n\n\n\n******************ELSEWHERE ON ZDNET!******************\n\nThrill your favorite techie with perfect presents at ZDNet Shopper.\nhttp://cgi.zdnet.com/slink?166139\n\nTen tips to help you attain CRM ROI.\nhttp://cgi.zdnet.com/slink?166140\n\nGet the lowdown on all the different wireless LAN standards.\nhttp://cgi.zdnet.com/slink?166141\n\nEditors\u0027 Top 5: Check out the best gifts money can buy.\nhttp://cgi.zdnet.com/slink?166142\n\nFind out about standardizing C# in Tech Update\u0027s special report.\nhttp://cgi.zdnet.com/slink?166143\n\n***********************************************************\n\n\n\n\nAnchorDesk comes to you free of charge as a\nservice of ZDNet. On its companion Web site,\nAnchorDesk includes full details on the stories\nyou see above, plus late-breaking news, links\nto in-depth information, and much more. Visit:\nhttp://www.anchordesk.com/\n\nTo subscribe, unsubscribe, or make changes to your subscription,\nplease go to:\n http://cgi.zdnet.com/slink?130228\n\nWe are sending this news alert to the address:\npallen@ENRON.COM\nPlease make sure to provide this address in\nall your e-mail messages to us.  Thanks!\n\nCopyright ? 2001 ZD Inc. ZDNet is a registered\nservice mark of ZD Inc. ZDNet Logo is a service\nmark of ZD Inc.\n\n\n ###",
