        "subject": "ANCHORDESK: 2002 in review: Not perfect, but it sure beat 2001",
        "body": "\n_____________________DAVID COURSEY_____________________\n\n2002 IN REVIEW: NOT PERFECT, BUT IT SURE BEAT 2001\n\n    Welcome to my 2002 Year in Review column, which\n         I feel very safe in asserting you are reading\n         here first. It\u0027s been a pretty good year, unless\n         of course you hate Microsoft, love Linux,\n         or were hoping HP and Compaq would unite. Read\n         on for my highlights of the year that\u0027s about\n         to happen.\n\nhttp://cgi.zdnet.com/slink?/adeskb/adt1231/2835377:8593142\n\n\n#\n\n\n_____________________NEWS ANALYSIS_____________________\n\nSylvia Carr\n\n    MS STALLING?... MORE JOBS IN 2002... CABLE WOES PERSIST...\n\n    Could Microsoft be stalling? The nine\n    states still pursing the software giant\u0027s\n    antitrust case sure think so. They\u0027ve\n    asked a federal judge to reject Microsoft\u0027s\n    request to delay the remedy hearings,\n    though Microsoft says any holdup is\n    the states\u0027 fault for seeking such a\n    tough remedy.\n\nhttp://cgi.zdnet.com/slink?/adeskb/adt1231/2835380:8593142\n\n\n\n\n\n_____________________EXPERT ADVICE_____________________\n\n\nStephan Somogyi\n\n  A LOOK AHEAD: HOW 5 TECHNOLOGIES WILL FARE IN 2002\n\n   2001 was a great year for tech, but what about\n         this coming year? Stephan takes a stab at predicting\n         what will happen--and what he hopes will happen--with\n         Mac OS X, FireWire, gaming, and more.\n\n   http://cgi.zdnet.com/slink?/adeskb/adt1231/2835365:8593142\n\n\n                        \u003e \u003e \u003e \u003e \u003e\n\n\nRobert Vamosi\n\n\n   PATCH YOUR NEW WINDOWS XP MACHINE--NOW!\n\n   Been a little out of it during the holidays?\n         Then you may have missed the news about a security\n         vulnerability for Windows discovered a few\n         days before Christmas. Robert tells you what\u0027s\n         at stake and how to protect your PC.\n\n   http://cgi.zdnet.com/slink?/adeskb/adt1231/2835369:8593142\n\n\n                        \u003e \u003e \u003e \u003e \u003e\n\n\nPreston Gralla\n\n  LOST A FILE? NO PROBLEM! FIND IT WITH THESE 3 TOOLS\n\n   If your New Year\u0027s resolution is to get organized,\n         you\u0027re in luck. Preston recommends three\n         downloads that help you straighten up the\n         data filling up your PC, and locate any files\n         you may have lost.\n\n   http://cgi.zdnet.com/slink?/adeskb/adt1231/2835361:8593142\n\n\n                        \u003e \u003e \u003e \u003e \u003e\n\n\n_____________________CRUCIAL CLICKS_____________________\nMake sure you see these features on ZDNet today!\n\nWHEN WILL THE WINDOWS UPGRADES STOP?\nhttp://www.zdnet.com/techupdate/stories/main/0,14179,2830105,00.html\n\nCHECK OUT SONY\u0027S MUSIC MACHINE\nhttp://www.zdnet.com/supercenter/stories/overview/0,12069,542627,00.html\n\nHOW TO PROFIT FROM E-MEETINGS\nhttp://www.zdnet.com/techupdate/stories/main/0,14179,2830106,00.html\n\n\n\n******************ELSEWHERE ON ZDNET!******************\n\nThrill your favorite techie with perfect presents at ZDNet Shopper.\nhttp://cgi.zdnet.com/slink?166139\n\nTen tips to help you attain CRM ROI.\nhttp://cgi.zdnet.com/slink?166140\n\nGet the lowdown on all the different wireless LAN standards.\nhttp://cgi.zdnet.com/slink?166141\n\nEditors\u0027 Top 5: Check out the best gifts money can buy.\nhttp://cgi.zdnet.com/slink?166142\n\nFind out about standardizing C# in Tech Update\u0027s special report.\nhttp://cgi.zdnet.com/slink?166143\n\n***********************************************************\n\n\n\n\nAnchorDesk comes to you free of charge as a\nservice of ZDNet. On its companion Web site,\nAnchorDesk includes full details on the stories\nyou see above, plus late-breaking news, links\nto in-depth information, and much more. Visit:\nhttp://www.anchordesk.com/\n\nTo subscribe, unsubscribe, or make changes to your subscription, please go to:\n http://cgi.zdnet.com/slink?130228\n\nWe are sending this news alert to the address:\npallen@ENRON.COM\nPlease make sure to provide this address in\nall your e-mail messages to us.  Thanks!\n\nCopyright ? 2001 ZD Inc. ZDNet is a registered\nservice mark of ZD Inc. ZDNet Logo is a service\nmark of ZD Inc.\n\n\n ###",
        "date": "2002-01-01T06:54:34.000+0000"
